* {
  font-family: 'Press Start 2P', cursive;
}

.container {
  width: 800px;
  margin: 100px auto;
}

.main {
  height: 500px;
}

.space-evenly {
  display: flex;
  justify-content: space-evenly;
}

.btn-center {
  display: block;
  width: 200px;
  margin: 20px auto;
}

.hero {
  transform: scaleX(-1);
}

.row {
  display: block;
  padding: 20px;
}

.half {
  display: inline-block;
  height: 250px;
  width: 320px;
  vertical-align: top;
}

.mon {
  width: 150px;
}